export default {
	data() {
		return {
			inputText:'',
			userInfo:{}
		};
	},
	computed:{
		ByteLen() {
			var len = 0;
			for (var i = 0; i < this.inputText.length; i++) {
				var a = this.inputText.charAt(i);
				if (a.match(/[^\x00-\xff]/ig) != null) {
					len += 2;
				} else {
					len += 1;
				}
			}
			return len;
		}
	},
	onLoad(e) {
		this.userInfo = JSON.parse(e.info)
		this.inputText = this.userInfo.signature;
	},
	methods: {
		clearText(){
			this.inputText = ''
		},
		changeSign(){
			if(this.ByteLen > 50 || this.ByteLen < 0){
				uni.showToast({
					title:'昵称长度为0-50个字符',
					icon:'none'
				})
				return false;
			}
			if(this.inputText == this.userInfo.signature){
				uni.showToast({
					title:'与原昵称相同',
					icon:'none'
				})
				return false;
			}
			this.$uniRequest(
				this.$baseUrl+'/v1/member/edit',
				{
					birthday:this.userInfo.birthday,
					sex:this.userInfo.sex,
					nickname:this.userInfo.nickname,
					signature:this.inputText
				},
				(res)=>{
					if(res.data.code == 200){
						uni.showToast({
							title:'修改成功',
							mask:true
						})
						setTimeout(()=>{
							uni.navigateBack();
						},1000)
					}
				},
				false
			)
			
		}
	}
}
